.section.cta-w-image .container {
    padding: 50px 15px;
}
@media (min-width: $(desktop)) {
    .section.cta-w-image .container {
        padding: 50px 0;
    }
}
.section.cta-w-image .image {
    border-radius: 50%;
    overflow: hidden;
    margin: 0 auto;
    height: 300px;
    width: 300px;
}
@media (min-width: $(tablet)) {
    .section.cta-w-image .image {
        height: 200px;
        width: 200px;
    }
}
@media (min-width: $(desktop)) {
    .section.cta-w-image .image {
        margin-right: 0;
        margin-left: 175px;
    }
}
.section.cta-w-image .image figure {
    display: block;
    width: 100%;
    height: 100%;
}
.section.cta-w-image .image img {
    height: 100%;
    width: 100%;
    -o-object-fit: cover;
       object-fit: cover;
    -o-object-position: 50% 50%;
       object-position: 50% 50%;
}
.section.cta-w-image .text {
    text-align: left;
}
@media (min-width: $(tablet)) {
    .section.cta-w-image .text {
        -webkit-box-flex: 0;
        -webkit-flex: 0 0 auto;
            -ms-flex: 0 0 auto;
                flex: 0 0 auto;
        width: calc(99.99% * 5/8 - (30px - 30px * 5/8));
        margin-left: auto;
    }
    .section.cta-w-image .text:nth-child(1n) {
        margin-right: 30px;
        margin-left: 0;
    }
    .section.cta-w-image .text:last-child {
        margin-right: 0;
    }
    .section.cta-w-image .text:nth-child(1n) {
        margin-right: 0;
        margin-left: auto;
    }
}
@media (min-width: $(desktop)) {
    .section.cta-w-image .text {
        text-align: left;
    }
}
